<template>
  <div id="disqus">
    <div id="disqus_thread"></div>
  </div>
</template>

<script>
export default {
  props: ['identifier'],
  data: ()=>{
    return {
      scriptID: 'disqus_script',
      load: true
    }
  },
  mounted(){
    if(!this.load) return
    const identifier = this.identifier

    const reset = ()=>{
      const thread = this.$el.querySelector('#disqus_thread')
      const wrap = this.$el
      const div = document.createElement('DIV')

      div.id = 'disqus_thread'
      
      wrap.removeChild(thread)
      wrap.appendChild(div)
      
      window.DISQUS.reset({
        reload: true,
        config: function () {
          this.page.identifier = identifier
          this.page.url = window.location.href
          this.page.title = identifier
          console.log(this.page)
        }
      })
    }

    const exist = document.getElementById(this.scriptID)
    if(exist) {
      reset()
      return
    }
    // Load script
    const d = document, s = d.createElement('script')
    s.async = true
    s.src = 'https://https-timtnleeproject-github-io-blog-2.disqus.com/embed.js'
    s.id = this.scriptID
    s.setAttribute('data-timestamp', +new Date());
    (d.head || d.body).appendChild(s)
    s.addEventListener('load',()=>{
      reset()
    })
  }
}
</script>
